import { request } from 'utils';

export const getListPaperType = () => request({
  url: '/api/PaperType',
  method: 'GET',
})
export const getPaperType = (id) => request({
  url: `/api/PaperType/${id}`,
  method: 'GET',
})
export const deletePaperType = (id) => request({
  url: `/api/PaperType/${id}`,
  method: 'DELETE',
})
export const updatePaperType = (id, values) => 
   request({
    url: `/api/PaperType/${id}`,
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'PUT',
  })

export const createPaperType = (values) => 
 request({
    url: '/api/PaperType',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'POST',
  })



